<template>
    <div class="gallery">
        <div class="gallery-head">
            <h2>GALLERY</h2>
        </div>
        <div class="gallery-container">
            <div class="gallery-item" v-for="image in images" :key="image.number" >
            <img :src="require('../assets/gallery/' +image.name)" alt="" @click="selectImage(image)" >
        </div>
        </div>

        <div class="image-modal" :class="{'modal-displayed': displayModal}" @click="closeImage">
            
            <img :src="require('../assets/gallery/' + selectedImage)" alt="">
        </div>
        
        
    </div>
</template>

<script>

import {eventBus } from '../eventBus/eventBus.js'
export default {

    data() {
        return {
            images: eventBus.images,
            selectedImage: 'gramyDlaSlawka1.jpg',
            displayModal: false
        }
    },
    methods: {
        selectImage(image) {
            this.selectedImage = image.name;
            
            this.closeImage();
        },
        closeImage() {
            this.displayModal = !this.displayModal;
            
        }
    }

}
</script>

<style scoped>
    .gallery {
    display: grid;
    overflow-x: hidden;
    position: relative;
        
    }

    .gallery-head {
        display: grid;
        margin: 1rem 1rem;
    }
    .gallery-head h2 {
        justify-self: center;
        font-size: 3rem;
    }

    .gallery-container {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        grid-gap: 0 .3rem;
    }

    .gallery-item img {
    height: auto;
    width: 100%;
    max-width: 100%;    
    transition: all .3s ease-in-out;
    cursor: pointer;
    }

    @media (min-width: 800px) {
        .gallery-item img {
            opacity: .8;
        }
    }

    .gallery-item img:hover {
        opacity: 1;
    }

    .image-modal {
        position: fixed;
        transform: translate(100%);
        transition: transform .4s ease-in-out;
        display: flex;
        justify-content: center;
        height: 100%;
        
        
        
    }

    .image-modal img {
    height: auto;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
    cursor: pointer;
    }

    .modal-displayed {
        transform: translate(0);
        
    }

    @media (max-width: 360px) {
        .image-modal {
            height: auto;
            position: fixed;
            margin-top: 50%;
        }
    }

</style>
